@import '../utils/_variables';

.pt-6, .py-6 {
    padding-top: 4rem !important;
}

.pb-6, .py-6 {
    padding-bottom: 4rem !important;
}

.pt-7, .py-7 {
    padding-top: 5rem !important;
}

.pb-7, .py-7 {
    padding-bottom: 5rem !important;
}

.position-cover {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
}

.list-style-none {
    list-style: none !important;
}

.tr-section {
    padding-top: 50px;
    padding-bottom: 50px;
}

.heading-line {
    overflow: hidden;
    text-transform: uppercase;
    font-weight: 500;
    color: #000;
    & > * {
        display: inline-block;
        position: relative;
        padding-bottom: 10px;
        &:before {
            content: "";
            position: absolute;
            bottom: 0;
            width: 50px;
            border-bottom: 2px solid $base-color;
            right: 100%;
            left: 50%;
            transform: translate(-50%, 0);
        }
    }
}

.thin-sub-title {
    margin-bottom: 70px;
    color: #101c3f;
    text-transform: uppercase;
}

h2.heading-line {
    font-size: 36px;
}

h3.heading-line {
    font-size: 28px;
}

@mixin transition($default-transition) {
    -webkit-transition: $default-transition;
    -moz-transition: $default-transition;
    -ms-transition: $default-transition;
    -o-transition: $default-transition;
    transition: $default-transition;
}

@mixin darkened-background {
    &:before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        background-color: rgba(0, 0, 0, 0.28);
    }
}

@mixin radius($border-radius) {
    -webkit-border-radius: $border-radius;
    -moz-border-radius: $border-radius;
    border-radius: $border-radius;
}

@mixin rotate($rotate) {
    -webkit-transform: rotate($rotate);
    -moz-transform: rotate($rotate);
    -ms-transform: rotate($rotate);
    -o-transform: rotate($rotate);
    transform: rotate($rotate);
}

@mixin scale($scale) {
    -webkit-transform: scale($scale);
    -moz-transform: scale($scale);
    -ms-transform: scale($scale);
    -o-transform: scale($scale);
    transform: scale($scale);
}

@mixin shadow($shadow) {
    -webkit-box-shadow: ($shadow);
    -moz-box-shadow: ($shadow);
    -ms-box-shadow: ($shadow);
    -o-box-shadow: ($shadow);
    box-shadow: ($shadow);
}

@mixin gradient($aside,$aside-invert,$color1,$color2) {
    background: -moz-linear-gradient($aside, $color1 0%, $color2 100%);
    background: -webkit-linear-gradient($aside, $color1 0%, $color2 100%);
    background: -o-linear-gradient($aside, $color1 0%, $color2 100%);
    background: -ms-linear-gradient($aside, $color1 0%, $color2 100%);
    background: linear-gradient(to $aside-invert, $color1 0%, $color2 100%);
}

@mixin center_transform() {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}

@mixin vertical_center_transform() {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    transform: translate(0, -50%);
    -moz-transform: translate(0, -50%);
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    -o-transform: translate(0, -50%);
}

@mixin button($button-bg, $button-border, $button-border-color, $button-pad, $button-size, $button-line-height, $height-button, $button-radius) {
    display: inline-block;
    vertical-align: top;
    margin: 0 0 0 0;
    padding: 10px $button-pad 0 $button-pad;
    background: $button-bg;
    border: $button-border solid $button-border-color;
    font-size: $button-size;
    line-height: $button-line-height;
    height: $height-button;
    position: relative;
    @include radius($button-radius);
}

@mixin font($font-s, $font-h, $font-c) {
    font-size: $font-s;
    line-height: $font-h;
    color: $font-c;
}

@mixin array-icon($color, $rotate-deg) {
    content: '';
    border: solid $color;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate($rotate-deg);
}

@mixin sticker-tag($background-color,$font-size) {
    @include radius(3px);
    background-color: $background-color;
    font-size: $font-size;
}
